.center{
  margin: 0 auto;
  display: block;
}

.hover-overlay{
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(61, 61, 61, 0.2);
  z-index: 10;
  transition: all ease-in-out 300ms;
  opacity: 0;
  display: block;

  ul{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom: 50px;
    list-style-type: none;
    margin-top: 100px;
    transition: all ease-in-out 300ms;

    li{
      color:#ffffff;
      font-size: 30px;
    }
  }
  &:hover{
   opacity: 1;
  }
}

.tile{
  position: relative;
  background: #cccccc;
  text-align: center;
  transition: all ease-in-out 300ms;
  cursor:pointer;



  &.bodhan{
    background: #fdfaf5;
  }

  &.lnt{
    background: #d5e5ea;
  }

  &.covervidz{
    background: #9d89b3;
  }

  &.bus{
    img{
      margin-top: 0;
      margin-bottom: 50px;
    }
  }

  img{
    margin-top: 100px;
    margin-bottom: 100px;
    transition: all ease-in-out 300ms;

  }


  &:hover{
    img{
      transform: scale(0.8);
    }
  }
  h3{
    color:rgba(0,0,0,0);
    transition: all ease-in-out 300ms;
  }
}

.navigation{
  li{
    a{
      color: #444;
    }
  }
}

.header{
  min-height: 500px;
  padding-top: 200px;
  h1{
    font-size: 65px;
    margin-bottom: 10px;
    margin-top: 10px;
    color:#ffffff;
  }

  p{
    font-size: 17px;
    color:#ffffff;
  }

  img.logo{
    width:70px;
  }
}

.what-i-do{
  padding: 34px 15px;
}

.what-i-use{
  padding: 48px 15px;
  font-size: 30px;
  color: #12456f;
  transition: all ease-in-out 300ms;
  opacity: 0.5;
  h3{
    color: #ffffff;
  }
  &.active{
    opacity: 1;
    .photoshop{
      .bar{
        width:95%;
      }
    }
    .illustrator{
      .bar{
        width:95%;
      }
    }
    .handrawing{
      .bar{
        width:80%;
      }
    }.html{
      .bar{
        width:35%;
      }
    }.css{
      .bar{
        width:30%;
      }
    }

  }
}

.graph-bar{
  height:4px;
  background: rgba(255, 255, 255, 0);
  width:100%;
  margin-top: 5px;
  border-radius: 3px;
  .bar{
    width:0%;
    background:#343e73;
    height:100%;
    transition: all ease-in-out 2000ms;
    border-radius: 3px;
  }

}

